.lb-effect-rand-opacity-container * {
  -webkit-animation-duration: inherit;
          animation-duration: inherit;
}

.toggle-popout {
  -webkit-animation-name: lb-effect-rand-opacity-popout;
          animation-name: lb-effect-rand-opacity-popout;
}

@-webkit-keyframes lb-effect-rand-opacity-popout {
  35% {
    opacity: 1;
  }
}

@keyframes lb-effect-rand-opacity-popout {
  35% {
    opacity: 1;
  }
}

.toggle-fadein {
  -webkit-animation-name: lb-effect-rand-opacity-fadein;
          animation-name: lb-effect-rand-opacity-fadein;
}

@-webkit-keyframes lb-effect-rand-opacity-fadein {
  35% {
    opacity: 0;
  }
}

@keyframes lb-effect-rand-opacity-fadein {
  35% {
    opacity: 0;
  }
}
